* {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin: 0;
}
#element::-webkit-scrollbar {
  width: 7px;
  background-color: #f9f9fd;
}
.dashboard-container {
  width: 90vw;
  margin: auto;
  box-sizing: border-box;
}
.count-contents {
  font-weight: 400;
  margin: 10px 0 0 5px;
}
.count-contents span {
  padding: 4px 7px;
  border-radius: 7px;
}
.count-contents .th {
  background-color: rgba(0, 255, 255, 0.251);
  color: rgb(0, 137, 206);
}
.count-contents .sub {
  background-color: rgba(123, 0, 255, 0.251);
  color: rgb(122, 0, 236);
}
.count-contents .cont {
  background-color: rgba(0, 255, 102, 0.251);
  color: rgb(0, 167, 39);
}
.themes-container {
  margin-top: 50px;
  border: 1px solid #ddd;
  border-radius: 15px;
  padding-top: 10px;
}
.theme-item {
  width: 98%;
  border: 1px solid rgb(206, 206, 206);
  background-color: rgb(221, 255, 235);
  border-radius: 10px;
  margin: 10px auto;
  position: relative;
}
.theme-item:active {
  cursor: grab;
}
.theme-item .sub-container {
  min-height: 50px;
}
.theme-item h2 {
  display: inline-block;
  max-width: 60%;
  margin: 5px 10px;
  font-size: 22px;
  font-weight: 500;
  text-transform: uppercase;
}
.theme-item .sub-item {
  cursor: default;
  margin: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  display: flex;
  flex-flow: column wrap;
  /* justify-content: center; */
  min-height: 43px;
  background-color: rgba(255, 255, 255, 0.49);
  position: relative;
  overflow: hidden;
  /* box-shadow: 2px 1px 2px rgb(230, 227, 227); */
}
.theme-item .sub-item:active {
  cursor: grab;
}
.theme-item .toolbar {
  width: max-content;
  height: 36px;
  justify-content: space-around;
  align-items: center;
  position: absolute;
  display: flex;
  border-radius: 7px;
  background-color: rgb(221, 240, 235);
  right: 10px;
  top: 3px;
  box-shadow: 2px 1px 2px rgb(230, 227, 227);
  z-index: 5;
}
.dashboard-container .toolbar button, .add-btn {
  /* width:30px; */
  margin: auto 5px;
  background-color: rgb(158, 236, 236);
  border: 1px solid #ddd;
  border-radius: 5px;
  padding: 0 10px;
  height: 30px;
  cursor: pointer;
  /* box-shadow: 2px 1px 3px rgb(230, 227, 227); */
}
.theme-item .sub-item.active {
  /* cursor: grab; */
  padding-top: 15px;
}
.theme-item label {
  margin: 10px 0 0 10px;
  display: none;
  font-weight: 300;
}
.theme-item input {
  height: 26px;
  width: 90%;
  font-size: 16px;
  padding-left: 10px;
  border-radius: 10px;
  margin-left: 10px;
  border: 1px solid #ddd;
  max-width: 700px;
}
.theme-item .sub-item p {
  display: none;
  font-size: 12px;
  max-width: 95%;
  overflow-x: auto;
}
.theme-item .sub-item h3,
.theme-item .sub-item p {
  font-weight: 400;
  z-index: 0;
  margin: 2.5px 10px;
}
.theme-item .sub-item .content-container {
  display: none;
  flex-flow: row wrap;
  z-index: 3;
  width: 100%;
  cursor: default;
}
.theme-item .sub-item .content-container .content-info {
  overflow-x: auto;
  width: 100%;

}
.theme-item .sub-item.active .content-container {
  display: flex;
}
.theme-item .sub-item.active .content-container .drop-area {
  width: 100%;
  height: 100%;
  z-index: 10;
  background-color: white;
  border: 1px solid #ddd;
  font-size: 10px;
  text-align: center;
  cursor: pointer;
}
.theme-item .sub-item.active p,
.theme-item .sub-item.active label {
  display: initial;
  
}
.theme-item .sub-item .content-item {
  width: 48%;
  min-width: 300px;
  height: 90px;
  margin: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  display: flex;
  background-color: rgb(233, 255, 232);
  z-index: 7;
}
.theme-item .img-container {
  margin: 5px 10px;
  border: 1px solid rgb(162, 157, 157);
  border-radius: 5px;
  width: 80px;
  height: 70px;
  overflow: hidden;
  background-color: white;
  font-size: 12px;
  text-align: center;
  box-shadow: 2px 1px 2px rgb(231, 231, 231);
  position: relative;
}
.theme-item .img-container .del-btn {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 100;
}
.theme-item .img-container img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
